import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import './assets/main.css';

// Import MediaPipe Pose and TensorFlow.js
import { Pose } from '@mediapipe/pose';
import * as tf from '@tensorflow/tfjs-core'; // eslint-disable-line no-unused-vars
import '@tensorflow/tfjs-backend-webgl';

// Import VueLazyload for lazy loading images
import VueLazyload from 'vue-lazyload';

// Create Vue app instance
const app = createApp(App);

// Initialize MediaPipe Pose
const pose = new Pose({
  locateFile: (file) => {
    return `https://cdn.jsdelivr.net/npm/@mediapipe/pose/${file}`;
  }
});

// Configure pose options
pose.setOptions({
  modelComplexity: 1,
  smoothLandmarks: true,
  minDetectionConfidence: 0.5,
  minTrackingConfidence: 0.5
});

// Register VueLazyload
app.use(VueLazyload, {
  preLoad: 1.3,
  error: 'error.png', // Path to error image
  loading: 'loading.gif', // Path to loading image
  attempt: 1
});

app.config.globalProperties.$pose = pose;

// Use store and router as before
app.use(store).use(router);

// Mount the app
app.mount('#app');
