import axiosInstance from '@/utils/axios'; // Corrected path to utils

const state = {
  user: null,
  isAuthenticated: false,
};

const getters = {
  isAuthenticated: state => state.isAuthenticated,
  currentUser: state => state.user,
};

const actions = {
  async register({ commit }, userData) {
    try {
      const response = await axiosInstance.post('/auth', userData);
      const { data } = response;
      commit('setUser', data.data);
      commit('setIsAuthenticated', true);
      return data;
    } catch (error) {
      console.error('Registration error:', error);
      throw error;
    }
  },

  async login({ commit }, credentials) {
    try {
      const response = await axiosInstance.post('/auth/sign_in', credentials);
      const { data } = response;
      commit('setUser', data.data);
      commit('setIsAuthenticated', true);
      return data;
    } catch (error) {
      console.error('Login error:', error);
      throw error;
    }
  },

  async logout({ commit }) {
    try {
      await axiosInstance.delete('/auth/sign_out');
      commit('setUser', null);
      commit('setIsAuthenticated', false);
      localStorage.removeItem('access-token');
      localStorage.removeItem('client');
      localStorage.removeItem('uid');
    } catch (error) {
      console.error('Logout error:', error);
      throw error;
    }
  },
};

const mutations = {
  setUser(state, user) {
    state.user = user;
  },
  setIsAuthenticated(state, isAuthenticated) {
    state.isAuthenticated = isAuthenticated;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
